import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '../tools/withStyles';
import { Main } from '../components/Main';
import { Animation } from '../components/Animation';
import { Text } from '../components/Text';
import anime from 'animejs';

const styles = theme => ({
  root: {
    position: 'relative',
    padding: [20, 20, 30]
  },
  blogContainer: {
    maxWidth: '800px',
    margin: '0 auto',
    padding: '20px'
  },
  consoleContainer: {
    background: 'rgba(0, 0, 0, 0.7)',
    border: `1px solid ${theme.color.secondary.main}`,
    borderRadius: '8px',
    padding: '20px',
    fontFamily: 'monospace',
    position: 'relative',
    overflow: 'hidden',
    '&::before': {
      content: '""',
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      height: '2px',
      background: theme.color.secondary.main,
      boxShadow: `0 0 10px ${theme.color.secondary.main}`,
      animation: '$scanline 2s linear infinite'
    }
  },
  messageContainer: {
    marginBottom: '15px',
    opacity: 0,
    transform: 'translateY(20px)',
    '&.visible': {
      opacity: 1,
      transform: 'translateY(0)',
      transition: 'all 0.5s ease-out'
    }
  },
  typingText: {
    position: 'relative',
    '&::after': {
      content: '"|"',
      color: theme.color.secondary.main,
      animation: '$blink 1s infinite'
    }
  },
  loadingBar: {
    height: '4px',
    background: `linear-gradient(90deg, 
      ${theme.color.secondary.main}22, 
      ${theme.color.secondary.main}
    )`,
    position: 'relative',
    borderRadius: '2px',
    overflow: 'hidden',
    margin: '20px 0',
    '&::after': {
      content: '""',
      position: 'absolute',
      top: 0,
      left: '-20%',
      width: '20%',
      height: '100%',
      background: `linear-gradient(90deg, 
        transparent,
        ${theme.color.secondary.main},
        transparent
      )`,
      animation: '$loading 1.5s ease-in-out infinite'
    }
  },
  matrix: {
    position: 'absolute',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    pointerEvents: 'none',
    zIndex: 0
  },
  statusBox: {
    border: `1px solid ${theme.color.secondary.main}`,
    borderRadius: '4px',
    padding: '10px',
    marginTop: '20px',
    background: 'rgba(0, 0, 0, 0.3)',
    position: 'relative',
    overflow: 'hidden',
    '&::before': {
      content: '""',
      position: 'absolute',
      top: '-50%',
      left: '-50%',
      width: '200%',
      height: '200%',
      background: `linear-gradient(
        45deg,
        transparent,
        ${theme.color.secondary.main}22,
        transparent
      )`,
      animation: '$shine 3s linear infinite'
    }
  },
  '@keyframes blink': {
    '0%, 100%': { opacity: 1 },
    '50%': { opacity: 0 }
  },
  '@keyframes scanline': {
    '0%': { transform: 'translateX(-100%)' },
    '100%': { transform: 'translateX(100%)' }
  },
  '@keyframes loading': {
    '0%': { left: '-20%' },
    '100%': { left: '120%' }
  },
  '@keyframes shine': {
    '0%': { transform: 'translateX(-100%) translateY(-100%) rotate(45deg)' },
    '100%': { transform: 'translateX(100%) translateY(100%) rotate(45deg)' }
  }
});

class Blog extends React.Component {
  static propTypes = {
    classes: PropTypes.object
  };

  state = {
    currentMessageIndex: 0,
    messages: [
      "Initializing security protocols...",
      "Checking for vulnerabilities in the coffee machine...",
      "ERROR: Sleep.exe not found in my system",
      "Attempting to hack more hours into the day...",
      "ALERT: Detected excessive caffeine levels in bloodstream",
      "Loading bug bounty reports... and pizza delivery status",
      "WARNING: Brain.exe has stopped responding",
      "Deploying emergency memes as countermeasure",
      "Scanning for motivation.dll... file corrupted",
      "npm install more-energy@latest",
      "ERROR: Work-life-balance.js not found",
      "Deploying backup personality module...",
      "CRITICAL: Detected impending burnout",
      "Initiating emergency cat video protocol",
      "Loading progress... Like my CTF score (slowly)",
      "Executing nap.sh... Permission denied",
      "sudo nap --force --ignore-responsibilities",
      "ERROR: Reality conflicts with expectations",
      "Patching human limitations...",
      "Site will be ready after I hack the space-time continuum"
    ],
    matrixCharacters: []
  };

  componentDidMount() {
    this.messageInterval = setInterval(this.showNextMessage, 2000);
    this.initializeMatrix();
    this.startMatrixAnimation();
  }

  componentWillUnmount() {
    clearInterval(this.messageInterval);
    cancelAnimationFrame(this.matrixAnimation);
  }

  initializeMatrix = () => {
    const characters = '01'.split('');
    const matrixCharacters = [];
    const columns = Math.floor(window.innerWidth / 20);
    
    for (let i = 0; i < columns; i++) {
      matrixCharacters[i] = {
        x: i * 20,
        y: Math.random() * -100,
        char: characters[Math.floor(Math.random() * characters.length)],
        speed: Math.random() * 2 + 1
      };
    }

    this.setState({ matrixCharacters });
  };

  startMatrixAnimation = () => {
    const canvas = this.matrixCanvas;
    const ctx = canvas.getContext('2d');
    const { matrixCharacters } = this.state;

    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;

    const animate = () => {
      ctx.fillStyle = 'rgba(0, 0, 0, 0.05)';
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      ctx.fillStyle = '#0F0';
      ctx.font = '15px monospace';

      matrixCharacters.forEach(drop => {
        ctx.fillText(drop.char, drop.x, drop.y);
        drop.y += drop.speed;

        if (drop.y > canvas.height) {
          drop.y = Math.random() * -100;
          drop.char = '01'[Math.floor(Math.random() * 2)];
        }
      });

      this.matrixAnimation = requestAnimationFrame(animate);
    };

    animate();
  };

  showNextMessage = () => {
    const { currentMessageIndex, messages } = this.state;
    if (currentMessageIndex < messages.length - 1) {
      this.setState({ currentMessageIndex: currentMessageIndex + 1 });
      this.animateMessage();
    }
  };

  animateMessage = () => {
    anime({
      targets: '.message',
      opacity: [0, 1],
      translateY: [20, 0],
      duration: 800,
      easing: 'easeOutExpo',
      delay: anime.stagger(100)
    });
  };

  render() {
    const { classes } = this.props;
    const { currentMessageIndex, messages } = this.state;

    return (
      <Main className={classes.root}>
        <Animation show>
          <canvas 
            ref={ref => (this.matrixCanvas = ref)} 
            className={classes.matrix}
          />
          <div className={classes.blogContainer}>
            <div className={classes.consoleContainer}>
              {messages.slice(0, currentMessageIndex + 1).map((message, index) => (
                <div key={index} className={`${classes.messageContainer} message`}>
                  <Text className={classes.typingText}>{message}</Text>
                </div>
              ))}
              <div className={classes.loadingBar} />
              <div className={classes.statusBox}>
                <Text>
                  Blog system in progress... Please stand by "Dont waste your time, this might never come😂"
                </Text>
              </div>
            </div>
          </div>
        </Animation>
      </Main>
    );
  }
}

export default withStyles(styles)(Blog);
